html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.flex {
  display: flex;
}

.main-btn {
  padding: 12px 20px;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  color: var(--white-color);
  text-transform: uppercase;
  border: 4px solid var(--orange-color);
  transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
}

.main-btn:hover {
  background-color: var(--orange-color);
}

.main-btn:active {
  background-color: #A02511;
  border-color: #A02511;
}

.h2-title {
  position: relative;
  margin: 0;
  font-weight: 700;
  font-size: 36px;
  line-height: 45px;
  text-align: center;
  text-transform: uppercase;
}

.h2-title::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  left: 0;
  bottom: -30px;
  background-color: var(--orange-color);
}

.section-descr {
  margin: 0;
  font-weight: 500;
  font-size: 24px;
  line-height: 30px;
}

.breadcrumbs-container {
  padding: 16px 0;
}

.breadcrumbs-list {
  display: flex;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 16px;
}

.breadcrumbs__link {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: var(--black2-color);
}

.breadcrumbs__link--main {
  color: var(--orange-color);
}